.part {
  margin-top: 10vh;

  background-image: url(./backi_1.jpg);

  padding-top: 2vh;
}
.h_card {
  margin-top: 20px;
  margin-left: 13%;
  width: 74%;
  background-color: blue;
}
.column_1 {
  float: left;
  width: 70%;
  padding: 0rem 50px;
}
.column_2 {
  float: right;
}
/* Remove extra left and right margins, due to padding */
.row_1 {
  margin: 0 -5px;
}

/* Clear floats after the columns */
.row_1:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 600px) {
  .column_1 {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
  .part {
    background-image: url(./backi_2.jpg);
    background-position: center;
  }
}

.row_1 {
  margin: 0 -5px;
}
/* Style the counter cards */
.card_1 {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  padding: 10px 5px;
  text-align: center;
  background-color: #cccccc;

  background-position: center;
}

.data {
  font-size: 1.2rem;

  margin-right: 5%;
}
#bu_1 {
  padding: 8px 50px;
  margin: 5px;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0.883);
}
#bu_1:hover {
  background-color: rgb(4, 6, 128);
  transform: translateY(0);
  color: #cccccc;
}
#add_button {
  padding: 10px 7%;
  margin: 20px 25%;
}

.popup {
  background-color: rgba(0, 0, 0, 0.883);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 10;
  opacity: 1;
}
.popup_hidden {
  display: none;
}
#content {
  position: absolute;
  width: 50%;
  height: 200px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #cccccc;
  border-radius: 3px;
  border-bottom: 5px solid rgb(218, 55, 14);
  border-radius: 0 0 0 2.2rem;
  background-image: url(./backg_1.jpg);
  background-position: center;
  color: rgb(27, 27, 27);
}
#cancel {
  float: right;
  font-size: 1.5rem;
  margin-top: 4px;
  margin-right: 5px;
  cursor: pointer;
}
#cancel:hover {
  color: orangered;
}
#context {
  font-size: 1.75rem;
  margin-left: 10%;
  margin-top: 65px;
  height: 60px;
  word-spacing: 0.5rem;
  letter-spacing: 0.1rem;
}

#but_3 {
  float: right;
  margin-top: 10px;
  margin-right: 1.5%;
  padding: 8px 38px;
  background-color: rgb(27, 27, 27);
}
#but_3:hover {
  background-color: orangered;
  color: rgb(27, 27, 27);
}
@media screen and (max-width: 600px) {
  #add_button {
    padding: 10px 20%;
    margin: 20px 30%;
  }
}
