.background {
  width: 100%;
  height: 100vh;
  background-position: center;
}
.intro {
  width: 100%;
  height: 100vh;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.767);
}
.text {
  position: absolute;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  font-size: 2.5rem;
  font-weight: bold;
  color: #cccccc;
}
.p1:hover {
  color: brown;
}
.p3:hover {
  color: rgb(27, 71, 46);
}
.p2:hover {
  color: yellowgreen;
}

slider {
  display: block;
  width: 100%;
  height: 100%;
  background-color: #00000069;
  overflow: hidden;
  position: absolute;
}

slider > * {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #0000009f;

  animation: slide 12s infinite;
  overflow: hidden;
}

slide:nth-child(1) {
  left: 0%;
  animation-delay: -1s;
  background-image: url(background_10.jpg);
  background-size: cover;
  background-position: center;
}

slide:nth-child(2) {
  animation-delay: 2s;
  background-image: url(background_11.jpg);
  background-size: cover;
  background-position: center;
}

slide:nth-child(3) {
  animation-delay: 5s;
  background-image: url(background_12.jpg);
  background-size: cover;
  background-position: center;
}

slide:nth-child(4) {
  left: 0%;
  animation-delay: 8s;
  background-image: url(background_9.jpg);
  background-size: cover;
  background-position: center;
}

