@keyframes slide {
  0% {
    left: 100%;
    width: 100%;
  }
  5% {
    left: 0%;
  }
  25% {
    left: 0%;
  }
  30% {
    left: -100%;
    width: 100%;
  }
  30.0001% {
    left: -100%;
    width: 0%;
  }
  100% {
    left: 100%;
    width: 0%;
  }
}
@media screen and (max-width: 600px) {
  .text {
    left: 50%;
  }
}
@media screen and (max-width: 350px) {
  .text {
    left: 50%;
  }
}
