.navbar_1 {
  font-size: 0.8rem;
  position: fixed;
  width: 100%;
  overflow: hidden;
  border-style: none;
  height: 10vh;
}
.navbar_2 {
  background-color: black;
  z-index: 10;
}
.navbar_2 a:hover {
  background-color: #cccccc;
  color: rgb(0, 0, 0);
  text-decoration: none;
}
.navbar_2 a:active {
  text-decoration: none;
  background-color: greenyellow;
}

.navbar_logo {
  float: left;
  width: 3%;
  margin-top: 0.7rem;
  margin-left: 10%;
  display: flex;
  cursor: pointer;
}
.navbar_text {
  float: right;
  display: block;
  color: #cccccc;
  font-weight: 500;
  text-align: center;
  padding: 2.6vh 2%;
  text-decoration: none;
  font-size: 1rem;
  padding-right: 5%;
  margin-right: 10%;
  list-style: none;
}
.res_1 {
  position: fixed;
  z-index: 10;
  display: none;
}
#menu {
  width: 35px;
  height: 30px;
  margin: 30px 0 20px 20px;
  cursor: pointer;
}
.bar {
  height: 5px;
  width: 100%;
  background-color: blue;
  display: block;
  border-radius: 5px;
  transition: 0.3s ease;
}
#bar1 {
  transform: translateY(-4px);
}
#bar3 {
  transform: translateY(4px);
}

.nav li a {
  color: #fff;
  text-decoration: none;
}
.nav li a:hover {
  font-weight: bold;
}
.nav li {
  list-style: none;
  padding: 16px 0;
}
.nav {
  padding: 0;
  margin: 0 20px;
  transition: 0.3s ease;

  display: none;
}
.menu-bg,
#menu-bar {
  top: 0;
  left: 0;
  position: absolute;
}
.menu-bg {
  z-index: 1;
  width: 0;
  height: 0;
  background: radial-gradient(circle, rgb(66, 66, 248), rgb(1, 1, 211));
  margin: 30px 0 20px 20px;
  border-radius: 50%;
  transition: 0.3s ease;
}
#menu-bar {
  z-index: 2;
}
.change-bg {
  width: 550px;
  height: 540px;
  transform: translate(-60%, -30%);
}
.change .bar {
  background-color: white;
}
.change #bar1 {
  transform: translateY(4px) rotateZ(-45deg);
}
.change #bar3 {
  transform: translateY(-6px) rotateZ(+45deg);
}
.change #bar2 {
  opacity: 0;
}
.change {
  display: block;
}

@media screen and (max-width: 600px) {
  .navbar {
    height: 9vh;
  }
  .navbar_text {
    padding: 4vh 2%;
    padding-right: 5%;
    margin-right: 2%;
  }
  .navbar_logo {
    margin-top: 1.5rem;
    width: 5%;
  }
  .res {
    display: none;
  }
  .res_1 {
    display: block;
  }
}
