* {
  box-sizing: border-box;
}

body {
  font-family: arial, helvetica;
}
.back {
  background-color: rgb(255, 255, 255);
  font-family: "SourceSansProRegular", sans-serif;
  background-image: url(./back.jpg);

  background-position: center;
}
.text_1 {
  font-size: 2rem;
  padding-left: 10%;
  padding-top: 15vh;
  color: rgb(0, 0, 0);
}
.text_1:hover {
  color: chartreuse;
}
/* Float four columns side by side */
.column {
  float: left;
  width: 24.5%;
  padding: 30px 13px;
}

/* Remove extra left and right margins, due to padding */
.row {
  margin: 0 80px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns */
@media screen and (max-width: 900px) {
  .column {
    float: left;
    width: 40%;
    padding: 30px 13px;
  }
  .row {
    margin: 0 30px;
  }
}
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
  .row {
    margin: 0 30px;
  }
  .back {
    background-color: rgb(255, 255, 255);
    font-family: "SourceSansProRegular", sans-serif;
    background-image: url(./back_4.jpg);
    background-repeat: repeat;
    background-position: center;
  }
}

/* Style the counter cards */

/* Style the counter cards */
.card {
  text-align: center;
  background-color: white;
  border-bottom: 4px solid rgb(255, 61, 2);
  background-image: url(./back_2.jpg);
  background-position: bottom;
  border-radius: 20px 5px 45px 0;
  overflow: hidden;
  transition: all 0.2s;
}
.card:hover {
  box-shadow: 0 10px 15px rgba(255, 61, 2, 0.856);
  transform: translateY(-4px);
}
.card:active {
  box-shadow: 0 3px 7px rgba(255, 61, 2, 0.856);
  transform: translateY(4px);
}

img {
  width: 100%;
}
img:hover {
  transform: scale(1.1);
}

.text_2 {
  font-size: 0.9rem;
  padding: 20px 0;
}
.button_2 {
  padding: 15.5px 15px 11.5px 15px;
  margin-top: 8px;
  margin-right: 16px;
  background: rgb(0, 0, 0);
  font-size: 17px;
  border: none;
  cursor: pointer;
}
.button_2:hover {
  background-color: #cccccc;
}
.button {
  padding: 10px 15%;
  margin: 20px 0;
  font-size: 0.9rem;
  transition: all 0.2s;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: rgb(216, 216, 216);
  background-color: blue;
  border: none;
  border-radius: 3px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.856);
}

.button:hover {
  background-color: chartreuse;
  color: black;
  transform: translateY(-5px);
  box-shadow: 0 7px 15px rgba(0, 0, 0, 0.705);
}

.button:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.842);
  transform: translateY(4px);
}
